.all-clases {
  margin-top: 80px;
}

.header-clases {
  width: 100%;
  height: calc(100vh - 80px);
  overflow: hidden;
  background-size: contain;
}

.overly-header {
  width: 100%;
  height: calc(100% - 80px);
  background: #4c3e6670;
  z-index: 3;
  position: absolute;
  top: 80px;
}

.header-clases .background {
  width: 100%;
  height: 100%;
}

.clases-btn-est {
  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);
  background: #2675ad;
  border-radius: 20px;
  height: 30px;
  text-align: center;
  width: 390px;
  z-index: 4;
}

.clases-btn-est p {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  color: white;
  width: 150%;
  z-index: -3;
}

