.all-cursos {
  margin-top: 80px;
}

.header-cursos {
  width: 100%;
  height: calc(100vh - 80px);
  overflow: hidden;
  background-size: contain;
}

.overly-header{
  width: 100%;
  height: calc(100% - 80px);
  background: #4c3e6670;
  z-index: 3;
  position: absolute;
  top: 80px;
}

.header-cursos img {
  width: 100%;
  height: 100%;
}

.titulo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%) translateY(50%);
}

.cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-column-gap: 2.5%;
  grid-row-gap: 50px;
  margin: 15vh 5% 0% 5%;
  width: 90%;
}

.cards-cursos {
  transition: all ease 0.5s;
  width: 90%;
  height: 50vh;
  margin: 0px 0% 0% 0%;
  box-shadow: 0 0 16px rgb(0 0 0 / 10%);
  border-radius: 6px;
  position: relative;
  border: 1px solid #0000001a;
}

.cards-cursos:hover {
  transition: all ease 0.5s;
  transform: translateY(-5%);
  box-shadow: 0 0 25px rgb(0 0 0 / 20%);
  border-radius: 8px;
  border: 1px solid #00000033;
}
.titulo-cards {
  position: absolute;
  left: 50%;
  transform: translateX(-50%) translateY(30%);
  color: #4c3e66;
}

.cards-cursos img {
  width: 90%;
  border-radius: 5px;
  position: absolute;
  left: 50%;
  top: 20%;
  transform: translateX(-50%);
}

.texto-card_container {
  width: 90%;
  position: absolute;
  bottom: 5%;
  left: 5%;
  text-align: center;
  color: #7e5b96;
  font-size: 14px;
  font-weight: 600;
}

.btn-cursos:hover {
  transition: all 0.5s;
  background: #7e5b96;
}

.btn-cursos {
  transition: all 0.5s;
  background: #4c3e66;
  color: white;
  font-weight: bolder;
  border: none;
  outline: none;
  padding: 10px 20px 10px 20px;
  border-radius: 35px;
}
